.container {
  width: 100%;
  height: 100%;
  border: 1px solid red;
  position: relative;
  .toolbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 44px;
    border: 1px solid green;
    z-index: 9;
  }
  .graph {
    width: 100%;
    height: 100%;
    cursor: default;
    .selected {
      border: 1px solid #00a3fe;
    }
  }
  //   .config {
  //     position: absolute;
  //     top: 10px;
  //   }
}

.myport {
  width: 100%;
  height: 100%;
  border: 1px solid #808080;
  border-radius: 100%;
  background: #eee;
}
